.home-container {
  padding: 50px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.hero-section {
  text-align: center;
  padding: 80px 0;
  background: linear-gradient(to right, #f8f9fa, #e9ecef);
  border-radius: 10px;
  margin-bottom: 50px;
}

.hero-section h1 {
  font-size: 3rem;
  margin-bottom: 10px;
  color: #0d6efd;
}

.hero-section h2 {
  font-size: 1.8rem;
  margin-bottom: 20px;
  color: #333;
}

.hero-section p {
  font-size: 1.2rem;
  margin-bottom: 30px;
  color: #6c757d;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.cta-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.skills-section {
  padding: 50px 0;
}

.skills-section h2 {
  text-align: center;
  margin-bottom: 40px;
  font-size: 2rem;
  color: #333;
}

.skills-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 30px;
}

.skill-card {
  background: white;
  border-radius: 10px;
  padding: 30px 20px;
  text-align: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.skill-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

.skill-card i {
  font-size: 3rem;
  margin-bottom: 15px;
  color: #0d6efd;
}

.skill-card h3 {
  font-size: 1.2rem;
  color: #333;
}

@media screen and (max-width: 768px) {
  .hero-section {
    padding: 50px 20px;
  }

  .hero-section h1 {
    font-size: 2.5rem;
  }

  .hero-section h2 {
    font-size: 1.5rem;
  }

  .hero-section p {
    font-size: 1rem;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .skills-container {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 20px;
  }
}